<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder="" style="width:240px;"></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.name" placeholder="" style="width:240px;"></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.name" placeholder="" style="width:240px;"></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.name" placeholder="/" style="width:300px;"></el-input>
					<el-button type="info" plain v-on:click="getUsers" class="searchkey"></el-button>
				</el-form-item>
	
			</el-form>
		</el-col>

		<!---->
		<el-table :data="users" stripe highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="index">
			</el-table-column>
			<el-table-column type="selection">
			</el-table-column>
			<el-table-column prop="name" label="">
			</el-table-column>
			<el-table-column prop="sex" label="" :formatter="formatSex">
			</el-table-column>
			<el-table-column prop="age" label="">
			</el-table-column>
			<el-table-column prop="age" label="">
			</el-table-column>
			<el-table-column prop="age" label="">
			</el-table-column>
			<el-table-column prop="age" label="">
			</el-table-column>
			<el-table-column prop="age" label="">
			</el-table-column>
			<el-table-column prop="age" label="">
			</el-table-column>
			<el-table-column label="">
				<template scope="scope">
					<el-button class="tablebtn" size="small" @click="handledetail(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
		
		<!---->
		<el-dialog title="" :visible.sync="limitedDeta">
				
		</el-dialog>
			
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				limitedDeta: false,//
				
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				addForm: {
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				}

			}
		},
		methods: {
			//
			formatSex: function (row, column) {
				return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//
			getUsers() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				this.listLoading = true;
				//NProgress.start();
				getUserListPage(para).then((res) => {
					this.total = res.data.total;
					this.users = res.data.users;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//		
			handledetail(index, row) {
				this.limitedDeta = true;
				console.log(index, row);
				},		
			selsChange: function (sels) {
				this.sels = sels;
			},
		},
		mounted() {
			this.getUsers();
		}
	}

</script>

<style>

</style>


